.p-news {
  padding: 100px 0 40px;
  @include media(lg) {
    max-width: 1000px;
    margin: 0 auto;
    padding: $pd_pc;
  }
}

.p-news__head-photo {
  width: 100%;
  height: auto;
}

.p-news__container {
  padding: $pd_sp;

  @include media(lg) {
    padding: 40px 0 80px;
  }
}

.p-news__item {
  margin-bottom: $mg_bottom_md;
}

.p-news__date {
  display: block;
  text-align: right;
  padding-bottom: 5px;
  border-bottom: 1px dotted $base_gray_light;
}

.p-news__thumbnail {
  width: 100%;
  height: auto;

  @include media(lg) {
    max-width: 500px;
  }
}
